.AddCategoryPage {
    text-align: center;
    width: 100%;
    height: auto;
  }
    
  .AddCategoryPage h1{
    color: rgb(25,150,170);
    font-size: 45px;
    font-weight: bold;
    line-height: 120px;
    /* position: relative;
    right: 480px; */
    margin-top: 10px;
    margin-bottom: 40px;
  }
     
  .AddCategory {
    width: 50%;
    height: 450px;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid #e9e9e9;
    padding: 10px;
    box-shadow: 3px 5px 20px 5px #e9e9e9;
    border-radius: 20px;
    margin-bottom: 220px;
  }
    
  form {
    float: left;
    box-sizing: border-box;
    width: 50%;
    max-width: 100%;
    height: auto;
    padding: 100px;
  }
  
  form .form-control {
    border: none;
  }
  
  form input {
    width: 400px;
    height: 40px;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 30px;
    border-radius: 4px;
    color: #333;
    font-size: 17px;
    outline: none;
  }
  
  .add-button {
    width: 100px;
    height: 50px;
    background-color: rgb(77,193,195);
    border: 1px solid rgb(77,193,195);
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 20px;
    margin-left: 20px;
  }
  
  .add-button:hover {
    background-color: rgb(25,150,170);
  }
  
  form h2 {
    font-size: 20px;
  }
  
  .cancel-button {
    width: 100px;
    height: 50px;
    background-color: rgb(132, 132, 132);
    color: #fff;
    margin-left: 20px;
    border: 1px solid;
    border-radius: 4px;
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 20px;
  }
  
  .cancel-button:hover {
    background-color: rgb(96, 96, 96);
  }
  
  .btn-container {
    display: flex;
    width: 600px;
    margin-top: 40px;
    position: relative;
    right: 8px;
  }
  
  .pn {
    inline-size: 1000px;
  }